import React, {FC} from 'react';
import './sideBar.scss'
import {faAddressCard, faFileSignature, faGraduationCap, faSuitcase} from '@fortawesome/free-solid-svg-icons';
import {LinkWithIcon} from '../../components/commons/buttons/LinkWithIcon';

export const Menu: FC = () => {
  return (
    <nav className="nav">
      <LinkWithIcon icon={faGraduationCap} to='/skills' title=''/>
      <LinkWithIcon icon={faFileSignature} to='/files' title=''/>
      <LinkWithIcon icon={faSuitcase} to='/projects' title=''/>
      <LinkWithIcon icon={faAddressCard} to='/contacts' title=''/>
    </nav>
  )
}

